<template>
  <el-menu
    :default-active="'/index'"
    router
    mode="horizontal"
    background-color="white"
    text-color="#222"
    active-text-color="red"
    style="min-width: 1500px">
    <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
      {{ item.navItem }}
      <el-submenu v-if="item.children != null" v-for="(sub,j) in item.children" :key = "j" :index="sub.name">
        <el-menu-item>{{ sub.navItem }}</el-menu-item>
      </el-submenu>
    </el-menu-item>
    <span style="position: absolute;padding-top: 20px;
    right: 38%;font-size: 20px;font-weight: bold">Welcome to Dz-blogs</span>
    <el-input
      placeholder="快速搜索..."
      prefix-icon="el-icon-search"
      size="medium"
      style="width: 300px;position:absolute;margin-top: 12px;right: 18%">
    </el-input>
    <i class="el-icon-menu" style="float:right;font-size: 25px;color: #222;padding-top: 8px"></i>
    <el-dropdown style="float:right;padding-top: 8px;color:black">
       <span class="el-dropdown-link" v-html="uName">
        <i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="dialogFormVisible = true">个人中心</el-dropdown-item>
        <el-dropdown-item>修改密码</el-dropdown-item>
        <el-dropdown-item @click.native="logout()">退出</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dialog title="个人信息" :visible.sync="dialogFormVisible">
      <el-form :model="personForm">
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input v-model="personForm.username" autocomplete="off" disabled = 'true'></el-input>
        </el-form-item>
        <el-form-item label="头像" :label-width="formLabelWidth">
          <el-upload
            action="#"
            list-type="picture-card"
            :auto-upload="false">
            <i slot="default" class="el-icon-plus"></i>
            <div slot="file" slot-scope="{file}">
              <img
                class="el-upload-list__item-thumbnail"
                :src="file.url" alt=""
              >
              <span class="el-upload-list__item-actions">
          <span
            class="el-upload-list__item-preview"
            @click="handlePictureCardPreview(file)"
          >
            <i class="el-icon-zoom-in"></i>
          </span>
          <span
            v-if="!disabled"
            class="el-upload-list__item-delete"
            @click="handleDownload(file)"
          >
            <i class="el-icon-download"></i>
          </span>
          <span
            v-if="!disabled"
            class="el-upload-list__item-delete"
            @click="handleRemove(file)"
          >
            <i class="el-icon-delete"></i>
          </span>
        </span>
            </div>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label="简介" :label-width="formLabelWidth" disabled = true>
          <el-input type="textarea" v-model="personForm.intro" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </el-menu>
</template>

<script>
export default {
  name: 'NavMenu',
  data () {
    return {
      navList: [
        {name: '/index', navItem: '首页'},
        {name: '/jotter', navItem: 'java'},
        {name: '/library', navItem: '数据库'},
        {name: '/admin', navItem: '中间件'}
      ],
      uName: JSON.parse(localStorage.getItem('user')).username,
      personForm: {
        username: 'admin',
        intro: '这个人很懒，什么都没留下..'
      },
      dialogFormVisible: false,
      formLabelWidth: '80px',
      dialogImageUrl: '',
      dialogVisible: false
    }
  },
  created () {
    // console.log(localStorage.getItem('user'))
  },
  methods: {
    // 注销
    logout () {
      console.log('logout')
      this.$confirm('确定要退出吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post('/logout').then((successRes) => {
          // 退出清除登录信息
          this.$store.commit('logout')
          this.$message(successRes.data.msg)
          this.$router.replace('/login')
        })
      })
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    }
  }
}
</script>

<style scoped>
  .el-dropdown-link {
    cursor: pointer;
    color: black;
    margin-right: 30px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  a{
    text-decoration: none;
  }
</style>



