import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Appindex from '@/components/home/Appindex'
import Home from '@/components/Home'
import Register from '../components/Register'
Vue.use(Router)

export default new Router({
  mode: 'hash', // 默认是Hash模式 修改为history模式
  routes: [
    {
      path: '/',
      redirect: '/login',
      name: 'init'
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '/index',
          name: 'Appindex',
          component: Appindex,
          meta: {
            requireAuth: true
          }
        }
      ]
    }
  ]
})



