<template>
  <el-form :model="registerForm" :rules = "rules2" ref="registerForm" class="login-container">
    <h3 class = "login-title">用户注册</h3>
    <el-form-item prop="lUsername">
      <el-input v-model="registerForm.lUsername" placeholder="请填写用户名"/>
    </el-form-item>
    <el-form-item prop="lPassword">
      <el-input v-model="registerForm.lPassword" type="password" placeholder="请填写密码"/>
    </el-form-item>
    <el-form-item prop="newPassword">
      <el-input v-model="registerForm.newPassword" type="password" placeholder="请再次填写密码"/>
    </el-form-item>
    <el-form-item style="width:100%">
      <el-button type="primary" style="width: 100%;border: none" @click="register('registerForm')"
                 v-on:keyup.enter="register">注册</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'Register',
  data () {
    var validPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.registerForm.lPassword !== '') {
          this.$refs.registerForm.validateField('password')
        }
        callback()
      }
    }
    var validNewPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.registerForm.lPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      registerForm: {
        lUsername: '',
        lPassword: '',
        newPassword: ''
      },
      rules2: {
        lUsername: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        lPassword: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: validPassword, trigger: blur }
        ],
        newPassword: [
          { required: true, message: '请输入确认密码', trigger: 'blur' },
          { validator: validNewPassword, trigger: blur }
        ]
      },
      checked: true,
      loading: false
    }
  },
  methods: {
    register (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert('通过')
          this.$axios.post('/register', {username: this.registerForm.lUsername, password: this.registerForm.lPassword})
            .then(successRes => {
              if (successRes.code === 200) {
                this.$message({message: '注册成功', type: 'success', duration: 1000})
                this.$router.go(0)
              } else {
                this.$message.error(successRes.data.msg)
              }
            })
        } else {
          this.$message.error('请填写信息')
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



