<template>
  <body id = "poster">
  <el-tabs class ="container" v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="登录" name="login">
      <el-form :model="loginForm" :rules = "rules" ref="loginForm" class="login-container">
        <h3 class = "login-title">登录界面</h3>
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" placeholder="请输入用户名"/>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" type="password" placeholder="请输入密码"/>
        </el-form-item>
        <el-form-item prop="rememberMe" >
          <el-checkbox v-model="loginForm.rememberMe" style="color:white">记住我</el-checkbox>
        </el-form-item>
        <el-form-item style="width:100%">
          <el-button type="primary" style="width: 100%;border: none" @click="login('loginForm')"
                     v-on:keyup.enter="login">登  录</el-button>
        </el-form-item>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="注册" name="register">
      <Register></Register>
    </el-tab-pane>
  </el-tabs>
  </body>
</template>

<script>
import Register from './Register'
export default {
  name: 'Login',
  components: {Register},
  data () {
    return {
      loginForm: {
        username: '',
        password: '',
        rememberMe: false
      },
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      },
      checked: true,
      loading: false,
      activeName: 'login'
    }
  },
  methods: {
    login (formName) {
      // eslint-disable-next-line no-unused-vars
      var _this = this
      // console.log('state:' + this.$store.state)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios
            .post('/login', {
              username: this.loginForm.username,
              password: this.loginForm.password
            }).then(successRes => {
              console.log(this.loginForm.rememberMe)
              if (successRes.data.code === 200) {
                _this.$message({message: '登录成功', type: 'success', duration: 1000})
                var data = successRes.data.data
                _this.$store.commit('login', data)
                var path = this.$route.query.redirect
                _this.$router.replace({path: path === '/' || path === undefined ? '/index' : path})
              } else {
                this.$message.error(successRes.data.msg)
              }
            })
            .catch(failResponse => {
            })
        } else {
          this.$message.error('请填写用户名密码')
        }
      })
    },
    handleClick (tab, event) {
      console.log(tab, event)
    }
  }
}
</script>

<style>
  .el-tabs__item{
    font-size: 16px !important;
    color:white;
  }
  .container{
    width:400px;
    padding: 15px 15px 15px 15px;
    margin: 230px auto 0;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login-container{
    border-radius: 15px;
    border:1px solid #eaeaea;
    width:350px;
    color:white;
    padding: 15px 15px 15px 15px;
    box-shadow: 0 0 25px #cac6c6;
  }
  #poster {
    background: url("../assets/earth.jpg") repeat center;
    height: 100%;
    width: 100%;
    position:absolute;
    left:0px; top:0px;
    background-size: cover;
  }
  body{
    margin: 0;
  }
</style>



